<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
    
        <!---->
        <el-table :data="cpaList" stripe style="width: 100%" v-loading="loading">
            <el-table-column label="" prop="code" align="center"></el-table-column>
            <el-table-column label="" prop="name" align="center"></el-table-column>
            <el-table-column label="" prop="currency" align="center"></el-table-column>
            <el-table-column label="" prop="active" align="center"></el-table-column>
            <el-table-column label="" prop="isThirdPartyApi" align="center"></el-table-column>
            <el-table-column label="" prop="authToken" align="center"></el-table-column>
        </el-table>
        <!---->
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            
            //
            loading: false,
            cpaList: [],
            
        }
    },

    methods: {
        //cpa
        getcpaList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: this.searchForm }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaList = data.body.list;
                }
            })
        }
    },
    created() {
        this.getcpaList()
    }

}
</script>




