<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>{{partners}}</el-breadcrumb-item>
                <el-breadcrumb-item :to="{path:'/ec/cpa-goods/'+searchForm.cpaCode}"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--  -->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <el-form-item label="">
                    <el-input v-model="searchForm.goodsName" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.goodsSn" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.minshopPrice" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.maxshopPrice" placeholder="" size="small"></el-input>
                </el-form-item>
                <!-- <el-form-item label="">
                    <el-select v-model="searchForm.logisticsType" size="small" clearable >                              
                        <el-option :value="item.logisticsCode" :label="item.logisticsName" v-for="item in logisticsList" :key="item.logisticsCode"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="" prop="brandId">
                    <el-autocomplete v-model="searchForm.brandName" :fetch-suggestions="remoteGetBrand" placeholder="" :trigger-on-focus="false" size="small" :value-key="'brandName'" @select="brandSelect"></el-autocomplete>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt"> </el-button>
                </el-form-item>
            </el-form>
        </div>
        <!---->
        <div class="basth">
            <el-button type="primary" size="small" @click="doAction('')"></el-button>
        </div>
        <el-table :data="cpaGoodsList" border stripe style="width: 100%" v-loading="loading" @selection-change="handleSelectionChange" class="report" >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column label="" prop="goodsSn" align="left" width="180"></el-table-column>
            <el-table-column label="" prop="goodsName" align="left" min-width="250" show-overflow-tooltip> </el-table-column>
            <el-table-column label="" prop="brandName" align="left" width="180" show-overflow-tooltip> </el-table-column>
            <!-- <el-table-column label="" prop="logisticsTypeName" align="left" width="150"> </el-table-column> -->
            <el-table-column label="()" prop="shopPrice" align="right" width="120"> 
                <template slot-scope="scope">
                    {{scope.row.shopPrice?(scope.row.shopPrice/100):scope.row.shopPrice}}
                </template>
            </el-table-column>
            <el-table-column label="" width="100">
                <template slot-scope="scope">
                    <i title="" class="primary iconfont icon-add" @click="doAction(scope.row.goodsId,scope.row.goodsSn)"></i>
                </template>
            </el-table-column>
        </el-table>
        <!---->

        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.rows" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
    .basth {
        padding-bottom: 10px;
    }
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                goodsName: '',
                goodsSn: '',
                cpaCode: '',
                brandName: '',
                brandId: '',
                minshopPrice: '',
                maxshopPrice: '',
                logisticsType:'',
                offset: 0,
                page: 1,
                rows: this.myConfig.pageRows
            },
            //
            brandsList: [],
            optionList: [],
            //
            cpaList: [],
            //
            loading: false,
            brandLoading: false,
            cpaGoodsList: [],
            total: 0,
            //
            selectData: [],
            brandProps: {
                value: 'brandName',
                label: 'brandName'
            },
            logisticsList:[]
        }
    },
    computed: {
        partners() {
            for (let i in this.cpaList) {
                if (this.cpaList[i].code == this.searchForm.cpaCode) {
                    return this.cpaList[i].name
                }
            }

        }
    },
    methods: {
        //
        getcpaGoodsList() {
            if (this.searchForm.brandName == '') {
                this.searchForm.brandId = '';
            }
            this.loading = true;
            let params=JSON.parse(JSON.stringify(this.searchForm));
            params.minshopPrice=parseFloat(params.minshopPrice)*100;
            params.maxshopPrice=parseFloat(params.maxshopPrice)*100;
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/goods/not', { body: params }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaGoodsList = data.body.list;
                    this.total = data.body.total;
                }
            })
        },
        // 
        getBrands() {
            
            this.axios.post(this.myConfig.code.proxy + '/goods/api/brand/list', { body: {} }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.brandsList = data.body.rows;
                }
            })
        },
        //cpa
        getcpaList() {
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaList = data.body.list;
                }
            })
        },
        //
        handleCurrentChange(val) {
            this.searchForm.offset = (val - 1) * this.searchForm.rows;
            this.searchForm.page = val;
            this.getcpaGoodsList();
        },
        search_smt() {
            this.searchForm.page=1;
            this.searchForm.offset=0;
            this.getcpaGoodsList();
        },
        doAction(id, sn) {
            let _this = this;
            let list = [];
            if (id != '') {
                list = [{ goodsId: id, goodsSn: sn }];
            } else {
                if (this.selectData.length == 0) {
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                    return false;
                } else {
                    for (let i in this.selectData) {
                        list.push({ goodsId: this.selectData[i].goodsId, goodsSn: this.selectData[i].goodsSn })
                    }
                }
            }
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/goods/add', { body: { cpaCode: this.searchForm.cpaCode, list: list } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                    this.getcpaGoodsList();
                }
            })
        },
        //
        handleSelectionChange(val) {
            this.selectData = val;
        },
        //
        remoteGetBrand(queryString, cb) {
            let restaurants = this.brandsList;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : [];
            //  callback 
            cb(results);
        },
        createFilter(queryString) {
            return (restaurant) => {
                return (restaurant.brandName.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };

        },
        brandSelect(item) {
            this.searchForm.brandId = item.brandId;
        },
        // 
        getLogistics() {
        this.axios.post(this.myConfig.code.proxy + "/logistics/logistics/queryType", { body: { languageCode: "cn", status: 0 } }).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {
                this.logisticsList = data.body.logisticsTypes;
            }
            });
        },

    },
    created() {
        this.searchForm.cpaCode = this.$route.params.id;
        this.getcpaList();
        this.getBrands();
        this.getcpaGoodsList();
        // this.getLogistics();
    }

}
</script>




